import React, { forwardRef, useEffect, useRef, useState } from 'react';

const DATA_SPEED = 10;

const TagsList = [
  'Poke',
  'Adventures',
  'The Witcher',
  'Curry',
  'Technologies',
  'Hot yoga',
  'Anime',
  'Challenges',
  'Poke',
  'Adventures',
  'The Witcher',
  'Curry',
  'Technologies',
  'Hot yoga',
  'Anime',
  'Challenges',
];

const TagsStr = forwardRef(
  (
    props: { repeats: number; contentRef: React.MutableRefObject<HTMLDivElement> },
    ref: React.MutableRefObject<HTMLDivElement>
  ) => {
    return (
      <div className="scrolling-text" ref={ref} style={{ position: 'absolute', left: 0 }}>
        <div className="scrolling-text-content text-gray" ref={props.contentRef}>
          {Array(props.repeats)
            .fill(0)
            .map((_, n) =>
              TagsList.map((tag, i) => (
                <React.Fragment key={`${n}_${i}`}>
                  {tag}
                  &nbsp;∙&nbsp;
                </React.Fragment>
              ))
            )}
        </div>
      </div>
    );
  }
);

const Tags = () => {
  const cont = useRef();
  const scText1 = useRef<HTMLDivElement>();
  const scText2 = useRef<HTMLDivElement>();
  const scText1Content = useRef<HTMLDivElement>();
  const scText2Content = useRef<HTMLDivElement>();

  const [repeats, setRepeats] = useState(0);

  useEffect(() => {
    const scTextWidth = scText1.current?.clientWidth;
    if (scTextWidth > 0) {
      if (window.innerWidth > scTextWidth) {
        setRepeats(Math.ceil(window.innerWidth / scTextWidth));
      } else {
        setRepeats(1);
      }
    }
  }, []);

  useEffect(() => {
    if (repeats === 0) return; // attach handlers only when number of repeats is calculated

    const scTextWidth = scText1.current?.clientWidth;
    let offset = 0;

    const getScText = (pos: 'left' | 'right') => {
      const [left, right] =
        parseInt(scText1.current?.style?.left) < parseInt(scText2.current?.style?.left)
          ? [scText1.current, scText2.current]
          : [scText2.current, scText1.current];

      return pos === 'left' ? left : right;
    };

    const getLeftBound = (scText: HTMLDivElement) => {
      return parseInt(scText.style.left) + offset;
    };

    const getRightBound = (scText: HTMLDivElement) => {
      return getLeftBound(scText) + scTextWidth;
    };

    let skewResetTimeout: NodeJS.Timeout;
    const setTextSkew = (val: string) => {
      scText1Content.current.style.transform = `skewX(${val})`;
      scText2Content.current.style.transform = `skewX(${val})`;

      if (skewResetTimeout) clearTimeout(skewResetTimeout);

      skewResetTimeout = setTimeout(function () {
        scText1Content.current.style.transform = `skewX(0)`;
        scText2Content.current.style.transform = `skewX(0)`;
      }, 500);
    };

    const onWheel = n => {
      if (n?.deltaY > 0) {
        offset -= DATA_SPEED;
        setTextSkew('10deg');
      } else {
        offset += DATA_SPEED;
        setTextSkew('-10deg');
      }

      scText1.current.style.transform = 'translate3d(' + offset + 'px, 0, 0)';
      scText2.current.style.transform = 'translate3d(' + offset + 'px, 0, 0)';

      if (getLeftBound(getScText('left')) > 0) {
        const scText = getScText('right');
        scText.style.left = `${parseInt(scText.style.left) - scTextWidth}px`;
      }

      if (getRightBound(getScText('right')) < window.innerWidth) {
        const scText = getScText('left');
        scText.style.left = `${parseInt(scText.style.left) + scTextWidth}px`;
      }
    };

    window.addEventListener('wheel', onWheel);
    return () => {
      window.removeEventListener('wheel', onWheel);
    };
  }, [repeats]);

  return (
    <div className="content-wrap d-none d-xl-block">
      <div className="container left-to-right pt-100 pb-100" ref={cont}>
        <TagsStr ref={scText1} contentRef={scText1Content} repeats={repeats || 1} />
        <TagsStr ref={scText2} contentRef={scText2Content} repeats={repeats || 1} />
      </div>
    </div>
  );
};

export default Tags;
