import React, { useEffect } from 'react';
import AOS from 'aos';

import { Helmet } from 'react-helmet';

import 'aos/dist/aos.css';
import '../css/lkmvch.min.css';
import '../css/custom.css';

import octo3 from '../images/octo3.png';
import favicon from '../images/favicon.png';
import iconsvg from '../images/icon.svg';
import appletouch from '../images/apple-touch-icon.png'
import shareabout from '../images/share/share-about.png'

import { Provider as CursorProvider } from '../components/Common/Cursor';
import Footer from '../components/Common/Footer';
import AppBar from '../components/Common/AppBar';
import Intro from '../components/About/Intro';
import Tags from '../components/About/Tags';
import Timeline from '../components/About/Timeline';

const AboutPage = () => {
  useEffect(() => {
    AOS.init();
  }, []);

  return (
    <>
      <Helmet>
        <title>About Andrei Alikimovich</title>
        <meta name="description" content="I design digital products and websites" />
        <meta name="author" content="Andrei Alikimovich" />
        <meta name="theme-color" content="#1a1a1a"/>
        <link rel="icon" href={favicon} />
        <link rel="icon" href={iconsvg} type="image/svg+xml" />
        <link rel="apple-touch-icon" href={appletouch} />
        <meta property="og:image" content={shareabout} />
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:creator" content="@lkmvch" />
      </Helmet>
      <CursorProvider>
        <AppBar />
        <Intro />
        <Tags />

        <div className="content-wrap">
          <Timeline />
          <div className="octo-3">
            <img src={octo3} alt="Octopus" />
          </div>
        </div>

        <Footer />
      </CursorProvider>
    </>
  );
};

export default AboutPage;
